* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.App {
    width: 800px;
}

.post {
    display: flex;
    padding: 15px;
    border: 2px solid teal;
    margin-top: 15px;
    align-items: center;
    justify-content: space-between;
}
.post__btns {
    display: flex;
}
.post-enter {
    transform: translateX(-350px);
}
.post-enter-active {
    transform: translateX(0px);
    transition: all 500ms ease-in;
}
.post-exit {
    opacity: 1;
}
.post-exit-active {
    transform: translateX(-350px);
    transition: all 500ms ease-in;
}
.page__wrapper {
    margin-top: 30px;
    display: flex;
}
.page {
    padding: 10px;
    border: 1px solid teal;
    cursor: pointer;
}
.page__current {
    border: 2px solid orange;
    font-weight: 700;
}
.navbar {
    height: 50px;
    width: 100vw;
    display: flex;
    align-items: center;
    padding: 0 15px;
    background-color: lightgray;
}
.navbar__links {
    margin-left: auto;
}

.navbar__links a {
    color: teal;
    text-decoration: none;
    margin-right: 10px;

    transition: .3s linear;
}

.navbar__links a:hover {
    cursor: pointer;
    color: aquamarine;
}

